@import 'form-reg';

img {
	max-width: 100%; }

body {
	font-family: 'Source Sans Pro', sans-serif; }

* {
 padding: 0;
 margin: 0;
 box-sizing: border-box;
 outline: none; }

.container {
	max-width: 1114px;
	padding: 308px 0 0 0;
	min-height: 1290px;
	background-image: url(../img/form-reg/form-reg-bg.jpg);
 background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	@media screen and (max-width: 769px) {
	 padding: 100px 0;
  min-height: 100%; }
 @media screen and (max-width: 321px) {
  padding: 50px 0; } }
